import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

import { login } from "../../store/user";
import { HOME, REGISTER } from "../../constants/path";

import styles from "./index.module.scss";

function Register() {
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const isLoginProcessing = useSelector((state) => state.user.isLoggingIn);

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    useEffect(() => {
        const authToken = localStorage.getItem("authToken");

        if (authToken) {
            navigate(HOME);
        }
    }, []);

    const onEmailChange = (e) => {
        const newEmail = e.target.value;

        setEmail(newEmail);
    };

    const onPasswordChange = (e) => {
        const newPassword = e.target.value;

        setPassword(newPassword);
    };

    const onSubmit = (e) => {
        e.preventDefault();

        const formData = new FormData(e.target);
        const data = Object.fromEntries(formData.entries());

        dispatch(login(data))
            .then(() => {
                navigate(HOME);
            })
            .catch((err) => {});
    };

    const onRegisterClick = () => {
        navigate(REGISTER);
    };

    return (
        <div
            className={`${styles["container"]} flex-column flex-align-center flex-justify-center`}
        >
            <form
                className={`${styles["form"]} flex-column flex-align-center`}
                onSubmit={onSubmit}
            >
                <h3>Log In</h3>
                <input
                    className="text-field"
                    name="email"
                    placeholder="email"
                    type="email"
                    value={email}
                    onChange={onEmailChange}
                    required
                />
                <input
                    className="text-field"
                    name="password"
                    placeholder="Password"
                    type="password"
                    value={password}
                    onChange={onPasswordChange}
                    required
                />
                <button
                    className={`${styles["submit-btn"]} btn-outlined blue-btn`}
                    type="submit"
                    disabled={isLoginProcessing}
                >
                    Submit
                </button>
            </form>
            <div className={styles["register-container"]}>
                Don't have an account ?{" "}
                <span className="cursor-pointer" onClick={onRegisterClick}>
                    Register
                </span>
            </div>
        </div>
    );
}

export default Register;
